/* Color Pallete */

$blue: #007bff;
$blue-light: #007bff10;
$blue-hover: #007bff30;
$blue-light-opaque: #eff7ff;
$blue-mid: #007bff99;
$pblue: #4c75f2;
$pblue-light: #4c75f210;
$pblue-hover: #4c75f230;
$pblue-light-opaque: #eff7ff;
$pblue-mid: #4c75f299;
$indigo: #6610f2;
$purple: #6f42c1;
$pink: #e83e8c;
$cherry: #ff073a;
$cherry-mid: #ff073a99;
$cherry-light: #ff073a20;
$cherry-hover: #ff073a30;
$cherry-light-opaque: #ffe0e6;
$red: #dc3545;
$red-light: #dc354520;
$red-mid: #dc354599;
$orange: #fd7e14;
$orange-mid: #fd7e1499;
$orange-light: #fd7e1420;
$orange-hover: #fd7e1430;
$orange-light-opaque: #ffefe2;
$yellow: #ffc107;
$yellow-light: #ffc10720;
$yellow-hover: #ffc10730;
$yellow-mid: #ffc10799;
$yellow-light-opaque: #fff7e0;
$green: #28a745;
$green-light: #28a74520;
$green-hover: #28a74530;
$green-mid: #28a74599;
$teal: #20c997;
$cyan: #17a2b8;
$white: #fff;
$gray: #6c757d;
$gray-light: #6c757d10;
$gray-hover: #6c757d20;
$gray-mid: #6c757d99;
$gray-dark: #343a40;
$gray-opaque: #f1f1f1;
$primary: #007bff;
$secondary: #6c757d;
$success: #28a745;
$info: #17a2b8;
$warning: #ffc107;
$danger: #dc3545;
$light: #f8f9fa;
$dark: #343a40;
$purple: #201aa2dd;
$purple-light: #201aa220;
$purple-hover: #201aa230;
$purple-mid: #201aa299;
$purple-light-opaque: #e3e2f3;
$pink: #fb5581;
$pink-light: #ffa8cb10;
$pink-hover: #ffa8cb30;
$pink-mid: #ffa8cb99;

.App{
  position: relative;
  font-family: 'archia';
  font-weight: 400;
  color:$blue
}
.error{
  text-align:center;
  color:$danger;
  font-size:1.2rem;
  border-bottom: 2px solid red;
  display: inline-block;
  margin-left:43% ;
}

.navbar{
  text-align: center;
  background: $dark;
  height:50px;
  width:100%;
  position: relative;
  bottom: 20px;
  
  h2{
    background: $dark;
  }

}

.hidden{
  display: none;
}
.visible{
  display:block;

}

.form
{
  background: lightgrey;
  font-size: 1.4rem;
  max-width:60%;
  border:2px solid $pblue;
  margin-left:  auto;
  margin-right: auto;
  margin-top:100px;
  border-radius: 10px;
  box-sizing: border-box;
  box-shadow: 1px 7px 7px black;
  @media (min-width:500px) {
    width:250px;

  }
  & *{
    display:block;
    text-align: center;
    margin:5% auto;
  
  }

} 

button{
  border-radius:10px;
  width:120px;
  height:50px;
  background:$green;
  color:$cherry

}
.userbutton{
  margin-left:10px ;
}
.userpage{
  height:70px;
 
  position: relative;
}
.userposts{
  display: flex;
  flex-wrap:wrap;
  height: 90px;
  flex-direction: row-reverse;
  /* height: 100vw; */
  justify-content: flex-end;
}
  
  // @media(min-width: 500px){
  //   flex-flow: row-reverse;
  //   width:45%
  // }

  // @media(max-width:500px){
  //   display:block;
  // }
  
  .title{
    height: 50px;
    margin:10px;
    border:3px solid $blue-light;
    border-radius: 10px;
  } 
 .content{
   height:63%;
   margin:10px
 }


 .user{
    width:350px;
    height: 400px;
   margin:20px;
   
   @media(max-width:500px){
    width:80%;
    height:300px;
  }
    
    border:3px solid $purple;
    border-radius: 10px;
    background:grey;
    

    textarea{
      width:100%;
      height: 100%;
    }
 
    button{
      
      width: 60px;
      display: inline-block;
    }
    button:first-of-type{
      background: $danger;
      color:$info
    }
  }

